import Web3 from 'web3';
import store from '../store/index';
import { Toast } from 'vant';
import { i18n } from './i18n';
import { isProd } from './config';
import { guid, count } from '@/storage';
count.set(0);
const web3Data = {
	web3: {},
	t: i18n.global.t,
	factoryContract: {},
	chainInfo: {
		chainId: isProd ? 56 : 97,
		chainName: isProd
			? 'Binance Smart Chain Mainnet'
			: 'Binance Smart Chain Testnet',
		rpcUrls: ['https://bsc-dataseed.binance.org'],
		blockExplorerUrls: ['https://bscscan.com/']
	},
	web3Provider: null,
	getBlance: (account) => {
		web3Data.web3.eth.getBalance(account).then((amount) => {
			store.commit(
				'updateCurrentBalance',
				web3Data.web3.utils.fromWei(amount, 'ether')
			);
		});
	},
	initWeb3: async () => {
		if (window.ethereum) {
			web3Data.web3Provider = window.ethereum;
			window.ethereum.on('accountsChanged', (accounts) => {
				if (accounts.length) {
					let address = '';
					try {
						address = web3Data.web3.utils.toChecksumAddress(accounts[0]);
					} catch (error) {
						console.log(error);
					}
					store.commit('updateAccount', [address]);
				}
			});
			web3Data.web3 = new Web3(web3Data.web3Provider);
			try {
				await window.ethereum.enable();
			} catch (error) {
				Promise.reject(error);
			}
		} else {
			if (window.web3) {
				web3Data.web3Provider = window.web3.currentProvider;
				web3Data.web3 = new Web3(web3Data.web3Provider);
			} else {
				let num = count.get() || 0;
				if (Number(num) < 5) {
					count.set(Number(num) + 1);
					setTimeout(() => {
						web3Data.initWeb3();
					}, 500);
				} else {
					Toast(
						web3Data.t(
							'web3.暫未檢測到錢包外掛，請安裝MetaMask錢包， 或在imtoken里打開'
						)
					);
				}
			}
		}
		store.commit('updateProvider', web3Data.web3);
		web3Data.web3.eth.getAccounts().then((accounts) => {
			if (accounts.length) {
				let address = '';
				try {
					address = web3Data.web3.utils.toChecksumAddress(accounts[0]);
					store.commit('updateAccount', [address]);
				} catch (error) {
					console.log(error);
				}
			}
			return web3Data.getBlance(accounts);
		});
	}
};

export default web3Data;
