import { createRouter, createWebHashHistory } from 'vue-router';

const routes = [
	{
		path: '/',
		name: 'Index',
		component: () => import('@/views/index'),
		meta: {
			title: ''
			// keepAlive: true
		}
	},
	{
		path: '/notice',
		name: 'Notice',
		component: () => import('@/views/notice')
	},
	{
		path: '/notice/detail',
		name: 'NoticeDetail',
		component: () => import('@/views/notice/detail')
	},
	{
		path: '/ido0',
		name: 'Ido0',
		component: () => import('@/views/ido0')
	},
	{
		path: '/ido0/partner',
		name: 'Ido0Partner',
		component: () => import('@/views/ido0/partner')
	},
	{
		path: '/ido0/record',
		name: 'Ido0Record',
		component: () => import('@/views/ido0/record')
	},
	{
		path: '/idoa',
		name: 'Idoa',
		component: () => import('@/views/idoa')
	},
	{
		path: '/idoa/partner',
		name: 'IdoaPartner',
		component: () => import('@/views/idoa/partner')
	},
	{
		path: '/idoa/record',
		name: 'IdoaRecord',
		component: () => import('@/views/idoa/record')
	},
	{
		path: '/share',
		name: 'Share',
		component: () => import('@/views/share')
	}
];

const router = createRouter({
	history: createWebHashHistory(),
	routes
});

router.beforeEach((to) => {
	if (to.meta.title) {
		document.title = to.meta.title;
	}
});

export default router;
