import { createApp } from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import Vant, { Dialog } from 'vant';
import 'vant/lib/index.css';
import { intro, locale } from './storage';
import { i18n, setLang, getLang } from './utils/i18n';
import { formatTime } from './utils/public';
const lang = locale.get() || 'en';
import { Toast } from 'vant';
setLang(lang);
// 返回上一页
const goBack = () => {
	if (history.length > 0) {
		router.go(-1);
	} else {
		router.push({
			name: 'Index'
		});
	}
};
let queryParse = (str) => {
	if (!str || str === '0') {
		return {};
	}
	let dataArr = decodeURIComponent(str).split('&');
	let params = {};
	dataArr.forEach((query) => {
		let queryItem = query.split('=');
		if (queryItem.length === 1) {
			params.id = queryItem[0];
		} else {
			params[queryItem[0]] = queryItem[1];
		}
	});
	return params;
};

let href = window.location.href
	.replace(/#/g, '')
	.replace(/\?utm_source=tokenpocket/g, '');
if (href.includes('?')) {
	let search = href.slice(href.indexOf('?') + 1, href.length);
	let params = queryParse(search);
	if (params.intro) {
		// 是否有推薦邀请码
		intro.set(params.intro);
	}
}
const t = i18n.global.t;

const isDevelopment = (callback) => {
	// if (isProd) {
	return Toast(t('index.敬請期待'));
	// } else {
	// typeof callback === 'function' && callback();
	// }
};
function toRawType(value) {
	return Object.prototype.toString.call(value).slice(8, -1);
}
const errorformat = (error, callback) => {
	if (Object.prototype.hasOwnProperty.call(error, 'info') && error.info) {
		return console.log(error);
	}
	console.log('toRawType:', toRawType(error), 'error:', error);
	let errorStr = error;
	if (toRawType(error) === 'Error') {
		errorStr = String(error);
	} else {
		errorStr = JSON.stringify(error);
	}
	if (errorStr.indexOf('gas not enough') !== -1) {
		return Toast(t('error.餘額不足'));
	}
	if (errorStr.indexOf('Gas not enough') !== -1) {
		return Toast(t('error.餘額不足'));
	}
	if (errorStr.indexOf('transfer amount exceeds balance') !== -1) {
		return Toast(t('error.餘額不足'));
	}
	if (errorStr.indexOf('Referrer not valid') !== -1) {
		return Toast(t('error.該地址無質押，不能進行綁定'));
	}
	if (errorStr.indexOf('insufficient balance') !== -1) {
		return Toast(t('error.餘額不足'));
	}
	if (errorStr.indexOf('Already bought') !== -1) {
		return Toast(t('error.每個地址只能購買一次'));
	}
	if (errorStr.indexOf('transfer amount exceeds allowance') !== -1) {
		return Toast(t('error.transfer amount exceeds allowance'));
	}
	if (errorStr.indexOf('User denied transaction') !== -1) {
		return;
	}
	if (errorStr.indexOf('insufficient allowance') !== -1) {
		return Toast(t('error.insufficient allowance'));
	}
	if (errorStr.indexOf('invalid address') !== -1) {
		return Toast(t('error.invalid address'));
	}
	if (errorStr.indexOf('is disable') !== -1) {
		return Toast(t('error.is disable'));
	}
	if (errorStr.indexOf('No profit') !== -1) {
		return Toast(t('error.沒有可領取收益'));
	}
	if (errorStr.indexOf('The group has been completed') !== -1) {
		return Toast(t('error.團隊私募已完成'));
	}
	if (errorStr.indexOf('intro is not spot') !== -1) {
		return Toast(t('error.無效推薦地址'));
	}
	if (errorStr.indexOf('pre-miners has been sold out') !== -1) {
		return Toast(t('error.一期IDO已結束'));
	}
	if (errorStr.indexOf('pre-miners has not been sold out') !== -1) {
		return Toast(t('error.IDO未結束，不能退回'));
	}
	if (errorStr.indexOf('Round b ido has ended') !== -1) {
		return Toast(t('error.二期IDO已結束'));
	}
	if (errorStr.indexOf('transferFrom failed') !== -1) {
		return Toast(t('error.餘額不足'));
	}
	if (errorStr.indexOf('Has participated in round a ido') !== -1) {
		return Toast(t('error.此地址已參與過一輪IDO'));
	}
	if (errorStr.indexOf('intro error') !== -1) {
		return Toast(t('error.超級節點地址無效'));
	}
	if (errorStr.indexOf('There is no binding recommender') !== -1) {
		return Toast(t('error.團隊長地址無效'));
	}
	if (errorStr.indexOf('The recommender is not a leader') !== -1) {
		return Toast(t('error.推薦地址不是團隊長'));
	}
	if (errorStr.indexOf('The group has been extended') !== -1) {
		return Toast(t('error.已退回'));
	}
	

	if (typeof callback === 'object') {
		callback();
	} else {
		Dialog.alert({
			message: errorStr
		});
	}
};

const pdfReportViewer = () => {
	let url = 'Top Player White Paper.pdf';
	if (getLang() === 'en') {
		url = 'Top Player White Paper.pdf';
	}
	window.open('/pdf/web/viewer.html?file=' + url);
};

const app = createApp(App);
app.config.globalProperties.$goBack = goBack;
app.config.globalProperties.$dateformat = formatTime;
app.config.globalProperties.$errorformat = errorformat;
app.config.globalProperties.$isDevelopment = isDevelopment;
app.config.globalProperties.$pdfReportViewer = pdfReportViewer;
app.config.globalProperties.$setErrorImg = (e) => {
	e.target.src = require('@/assets/images/not_found.png');
};

app
	.use(router)
	.use(store)
	.use(i18n)
	.use(Vant)
	.mount('#app');
